<template>
  <div v-resize="handleResize">
    <Row :gutter="16">
      <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="6" class="ivu-mb">
        <ChartCard title="访问量">
          <template #extra>
            <Tag color="green">日</Tag>
          </template>
          <template #footer>
            <Row>
              <Col span="12">总访问量</Col>
              <Col span="12" class="ivu-text-right">280 万</Col>
            </Row>
          </template>
          <template #total>
            <CountUp :end="25848" />
          </template>
          <div class="ivu-pt-8" v-height="42">
            <span class="ivu-inline-block ivu-mr">
              日同比
              <Trend flag="up">12.5%</Trend>
            </span>
            <span class="ivu-inline-block">
              周同比
              <Trend flag="down">5%</Trend>
            </span>
          </div>
        </ChartCard>
      </Col>
      <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="6" class="ivu-mb">
        <ChartCard title="销售额">
          <template #extra>
            <Tag color="blue">月</Tag>
          </template>
          <template #footer>
            <Row>
              <Col span="12">总销售额</Col>
              <Col span="12" class="ivu-text-right">68 万</Col>
            </Row>
          </template>
          <template #total> ￥<CountUp :end="12000" /> </template>
          <div class="ivu-pt-8" v-height="42">
            <Progress :percent="70" />
          </div>
        </ChartCard>
      </Col>
      <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="6" class="ivu-mb">
        <ChartCard title="订单量">
          <template #extra>
            <Tag color="red">周</Tag>
          </template>
          <template #footer>
            <Row>
              <Col span="12">转化率</Col>
              <Col span="12" class="ivu-text-right">60%</Col>
            </Row>
          </template>
          <template #total>
            <CountUp :end="1680" />
          </template>
          <MiniArea ref="orderChart" class="ivu-pt-8" :height="42" :options="dayVisits" />
        </ChartCard>
      </Col>
      <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="6" class="ivu-mb">
        <ChartCard title="新增用户">
          <template #extra>
            <Tooltip placement="top" transfer>
              <template #content>
                <div style="white-space: nowrap">指标说明</div>
              </template>
              <Icon type="ios-help-circle-outline" />
            </Tooltip>
          </template>
          <template #footer>
            <Row>
              <Col span="12">总用户</Col>
              <Col span="12" class="ivu-text-right">10800 人</Col>
            </Row>
          </template>
          <template #total>
            <CountUp :end="128" />
            <span v-font="24">位</span>
          </template>
          <div class="ivu-pt-8" v-height="42">
            <AvatarList size="small" :max="7" :list="avatarList" :excessStyle="excessStyle" />
          </div>
        </ChartCard>
      </Col>
    </Row>

    <Card shadow>
      <template #title>
        <Row align="middle" justify="center">
          <Col :span="8">
            <Avatar size="small" icon="ios-podium" v-color="'#1890ff'" v-bg-color="'#e6f7ff'" />
            <span class="ivu-pl-8">访问量</span>
          </Col>
          <Col :span="16" v-if="!isMobile" class="ivu-text-right">
            <RadioGroup type="button" class="ivu-mr-16" v-model="visitType" @on-change="handleVisitType">
              <Radio label="day">今天</Radio>
              <Radio label="week">本周</Radio>
              <Radio label="month">本月</Radio>
              <Radio label="year">今年</Radio>
            </RadioGroup>
            <DatePicker type="daterange" v-model="visitDate" :options="options" placement="bottom-end" placeholder="Select date" v-width="200" />
          </Col>
        </Row>
      </template>
      <Row :gutter="16">
        <Col :xl="17" :lg="17" :md="15" :sm="24" :xs="24" style="overflow: hidden">
          <h4>访问量趋势</h4>
          <ChartBar ref="visitChart" :height="280" :options="salesOptions" />
        </Col>
        <Col :xl="7" :lg="7" :md="9" :sm="24" :xs="24">
          <h4>访问量排行</h4>
          <RankList :list="rankList" />
        </Col>
      </Row>
    </Card>

    <Row :gutter="16" class="ivu-mt">
      <Col :xl="12" :lg="24" :md="24" :sm="24" :xs="24">
        <Card shadow v-height="480">
          <template #title>
            <Avatar size="small" icon="ios-search" v-color="'#722ed1'" v-bg-color="'#f9f0ff'" />
            <span class="ivu-pl-8">热门搜索</span>
          </template>
          <template #extra>
            <Dropdown trigger="click">
              <Icon type="md-more" />
              <template #list>
                <DropdownMenu>
                  <DropdownItem>操作一</DropdownItem>
                  <DropdownItem>操作二</DropdownItem>
                </DropdownMenu>
              </template>
            </Dropdown>
          </template>
          <Row :gutter="24">
            <Col :xs="24" :sm="12">
              <NumberInfo :total="'23,378'" :sub-total="16.8" status="up">
                <template #subTitle>
                  <span>搜索用户数</span>
                  <Tooltip content="指标说明" placement="top">
                    <Icon type="ios-information-circle-outline" class="ivu-ml-8" />
                  </Tooltip>
                </template>
              </NumberInfo>
              <MiniArea ref="miniArea2" :height="50" :options="userOptions" />
            </Col>
            <Col :xs="24" :sm="12">
              <NumberInfo :total="3.1" :sub-total="8.5" status="down">
                <template #subTitle>
                  <span>人均搜索次数</span>
                  <Tooltip content="指标说明" placement="top">
                    <Icon type="ios-information-circle-outline" class="ivu-ml-8" />
                  </Tooltip>
                </template>
              </NumberInfo>
              <MiniArea ref="miniArea3" :height="50" :options="searchOptions" />
            </Col>
          </Row>
          <div class="ivu-mt">
            <div class="ivu-admin-table-no-border">
              <Table size="small" :columns="searchColumns" :data="searchData">
                <template #range="{ row }">
                  <Trend :flag="row.status === 0 ? 'up' : 'down'"> {{ row.range }}%</Trend>
                </template>
              </Table>
              <div class="ivu-mt-8 ivu-text-center">
                <Page size="small" :total="searchData.length * 10" />
              </div>
            </div>
          </div>
        </Card>
      </Col>
      <Col :xl="12" :lg="24" :md="24" :sm="24" :xs="24">
        <Card shadow v-height="480">
          <template #title>
            <Avatar size="small" icon="ios-people" v-color="'#faad14'" v-bg-color="'#fffbe6'" />
            <span class="ivu-pl-8">用户画像</span>
          </template>
          <Row align="middle" justify="center">
            <Col span="8" class="ivu-text-center" style="color: rgb(51, 153, 255)">
              <Avatar icon="ios-man" size="large" style="color: rgb(51, 153, 255); background: rgb(214, 240, 255)" />
              <div class="ivu-mt-8">男性 65%</div>
            </Col>
            <Col span="8" class="ivu-text-center" style="color: rgb(190, 107, 224)">
              <Avatar icon="ios-woman" size="large" style="color: rgb(190, 107, 224); background: rgb(253, 240, 255)" />
              <div class="ivu-mt-8">女性 25%</div>
            </Col>
            <Col span="8" class="ivu-text-center" style="color: rgb(186, 189, 195)">
              <Avatar icon="md-man" size="large" style="color: rgb(186, 189, 195); background: rgb(230, 237, 245)" />
              <div class="ivu-mt-8">未知 10%</div>
            </Col>
          </Row>
          <div class="user-gender-grid ivu-mt">
            <Grid :col="25" :border="false" center square padding="2px">
              <GridItem v-for="item in 200" :key="item">
                <span v-bg-color="item <= 120 ? '#3399ff' : item <= 180 ? '#be6be0' : '#babdc3'"></span>
              </GridItem>
            </Grid>
          </div>
        </Card>
      </Col>
    </Row>
  </div>
</template>

<script>
  import dayjs from 'dayjs'
  import { CountUp, Grid, GridItem, NumberInfo, Trend } from 'view-ui-plus'
  import { debounce } from '@/utils/util'
  import { mixinDevice } from '@/utils/mixin'
  import { ChartCard, RankList } from './component'
  import { MiniArea, ChartBar } from '@/components'

  const rankList = () => {
    const rankList = []
    for (let i = 0; i < 7; i++) {
      rankList.push({
        name: `百草路 ${i + 1} 号店`,
        total: Math.random() * 1200 + 400
      })
    }
    rankList.sort((a, b) => b.total - a.total)
    return rankList.map(item => ({ ...item, total: item.total.toFixed(2) }))
  }

  const searchColumns = [
    {
      key: 'index',
      title: '排名',
      width: 90
    },
    {
      key: 'keyword',
      title: '搜索关键词'
    },
    {
      key: 'count',
      title: '用户数'
    },
    {
      key: 'range',
      title: '周涨幅',
      align: 'right',
      slot: 'range'
    }
  ]
  const searchData = []
  for (let i = 0; i < 5; i += 1) {
    searchData.push({
      index: i + 1,
      keyword: `搜索关键词-${i}`,
      count: Math.floor(Math.random() * 1000),
      range: Math.floor(Math.random() * 100),
      status: Math.floor((Math.random() * 10) % 2)
    })
  }

  const echartsBar = months => {
    const xData = []
    const yData = []
    for (let i = 0; i < months; i++) {
      xData.push(dayjs().subtract(i, 'month').format('YYYY-MM'))
      yData.push(Math.ceil(Math.random() * 1000 * 12) + 3000)
    }
    xData.reverse()
    return { xData, yData }
  }

  const fixedZero = value => {
    const date = new Date(value)
    const hour = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours()
    const minute = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes()
    return `${hour}:${minute}`
  }

  const recentHour = hours => {
    const time = new Date().getTime()
    const result = []
    for (let i = 0; i < hours; i++) {
      result.push(time - 1000 * 60 * 5 * i)
    }
    return result.map(item => fixedZero(item)).reverse()
  }

  const miniAreaData = hours => {
    const xData = recentHour(hours)
    const yData = []
    for (let i = 0; i < hours; i++) {
      yData.push(Math.ceil(Math.random() * 500) + 500)
    }
    return { xData, yData }
  }

  export default {
    name: 'Console',
    mixins: [mixinDevice],
    components: {
      CountUp,
      MiniArea,
      Grid,
      GridItem,
      RankList,
      ChartBar,
      ChartCard,
      Trend,
      NumberInfo
    },
    data() {
      return {
        dayVisits: { ...miniAreaData(12), color: '#00CC66' },
        salesOptions: { ...echartsBar(12), color: '#1495eb' },
        userOptions: { ...miniAreaData(12), color: '#1495eb' },
        searchOptions: { ...miniAreaData(12), color: '#1495eb' },
        rankList: rankList(),
        searchColumns,
        searchData,
        options: {
          shortcuts: [
            {
              text: '近一周',
              value() {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
                return [start, end]
              }
            },
            {
              text: '近一个月',
              value() {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
                return [start, end]
              }
            },
            {
              text: '近三个月',
              value() {
                const end = new Date()
                const start = new Date()
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
                return [start, end]
              }
            }
          ]
        },
        excessStyle: { color: '#f56a00', backgroundColor: '#fde3cf' },
        avatarList: [
          {
            src: 'https://dev-file.iviewui.com/BbnuuEiM0QXNPHVCvb3E2AFrawIjCkqW/avatar',
            tip: '史蒂夫·乔布斯'
          },
          {
            src: 'https://dev-file.iviewui.com/zhj85zgAfEjChCNIKT1LQENUIOyOYCaX/avatar',
            tip: '斯蒂夫·沃兹尼亚克'
          },
          {
            src: 'https://dev-file.iviewui.com/TkH54UozsINlex15TAMI00GElsfsKSiC/avatar',
            tip: '乔纳森·伊夫'
          },
          {
            src: 'https://dev-file.iviewui.com/xrzbBR99F6tYsDJPLNrvwhllowbuL7Gw/avatar',
            tip: '蒂姆·库克'
          },
          {
            src: 'https://dev-file.iviewui.com/bgrngoUb9A6UQ2kAwBFtnSNzhrh2qj1O/avatar',
            tip: '比尔·费尔南德斯'
          },
          {
            src: 'https://dev-file.iviewui.com/BbnuuEiM0QXNPHVCvb3E2AFrawIjCkqW/avatar',
            tip: '史蒂夫·乔布斯'
          },
          {
            src: 'https://dev-file.iviewui.com/zhj85zgAfEjChCNIKT1LQENUIOyOYCaX/avatar',
            tip: '斯蒂夫·沃兹尼亚克'
          },
          {
            src: 'https://dev-file.iviewui.com/TkH54UozsINlex15TAMI00GElsfsKSiC/avatar',
            tip: '乔纳森·伊夫'
          },
          {
            src: 'https://dev-file.iviewui.com/xrzbBR99F6tYsDJPLNrvwhllowbuL7Gw/avatar',
            tip: '蒂姆·库克'
          },
          {
            src: 'https://dev-file.iviewui.com/bgrngoUb9A6UQ2kAwBFtnSNzhrh2qj1O/avatar',
            tip: '比尔·费尔南德斯'
          }
        ],
        visitType: 'day',
        visitDate: [new Date(), new Date()]
      }
    },
    methods: {
      handleVisitType(visitType) {
        this.visitDate = [dayjs().startOf(visitType).valueOf(), dayjs().endOf(visitType).valueOf()]
        this.rankList = rankList()
      },
      handleResize() {
        debounce(this.$refs.visitChart?.handleResize())
        debounce(this.$refs.orderChart?.handleResize())
        debounce(this.$refs.miniArea2?.handleResize())
        debounce(this.$refs.miniArea3?.handleResize())
      }
    }
  }
</script>

<style lang="less">
  .user-gender-grid {
    .ivu-grid-item-main {
      height: 100%;
    }

    & span {
      display: block;
      height: 100%;
      border-radius: 2px;
      opacity: 0.75;
    }
  }
</style>
