<template>
  <Card class="monitor-card ant-text-center" shadow>
    <Avatar :icon="icon" :style="color" />
    <div class="monitor-card-number">{{ number }}</div>
    <div class="monitor-card-info-desc">{{ desc }}</div>
    <div class="monitor-card-info-extra">
      <slot name="extra"></slot>
    </div>
  </Card>
</template>

<script>
  export default {
    name: 'MonitorCard',
    props: {
      icon: {
        type: String,
        default: undefined
      },
      color: {
        type: Object,
        default: () => {}
      },
      number: {
        type: [String, Number],
        default: undefined
      },
      desc: {
        type: String,
        default: undefined
      }
    }
  }
</script>

<style lang="less">
  .monitor-card {
    &-number {
      font-size: 28px;
      font-weight: 700;
      margin: 8px 0 4px;
    }

    &-info-desc {
      color: #808695;
      font-size: 12px;
    }

    &-info-extra {
      height: 25px;
      margin-top: 10px;
      font-weight: 700;
      font-size: 14px;

      i {
        font-size: 18px;
      }
    }
  }
</style>
