<template>
  <div v-resize="handleResize">
    <Row :gutter="16" class="ivu-text-center">
      <Col class="ivu-mb" :xs="12" :sm="12" :md="12" :lg="12" :xl="6">
        <MonitorCard icon="ios-people" number="21.2k" desc="总访问人数" :color="{ color: '#40A9FF', background: '#E6F7FF' }">
          <template #extra>
            <AvatarList :max="5" size="small" :list="avatarList" />
          </template>
        </MonitorCard>
      </Col>
      <Col class="ivu-mb" :xs="12" :sm="12" :md="12" :lg="12" :xl="6">
        <MonitorCard icon="md-locate" number="1.8k" desc="点击量（近30天）" :color="{ color: '#9254DE', background: '#F9F0FF' }">
          <template #extra>
            <Trend flag="up" text-color reverse-color>110.5%</Trend>
          </template>
        </MonitorCard>
      </Col>
      <Col class="ivu-mb" :xs="12" :sm="12" :md="12" :lg="12" :xl="6">
        <MonitorCard icon="md-paper-plane" number="826.0" desc="到达量（近30天）" :color="{ color: '#FFA940', background: '#FFF7E6' }">
          <template #extra>
            <Trend flag="down" text-color reverse-color>15.5%</Trend>
          </template>
        </MonitorCard>
      </Col>
      <Col class="ivu-mb" :xs="12" :sm="12" :md="12" :lg="12" :xl="6">
        <MonitorCard icon="md-funnel" number="28.8 %" desc="转化率（近30天）" :color="{ color: '#40A9FF', background: '#E6F7FF' }">
          <template #extra>
            <Trend flag="up" text-color reverse-color>65.8%</Trend>
          </template>
        </MonitorCard>
      </Col>
    </Row>
    <Row :gutter="16">
      <Col class="ivu-mb" :xs="24" :sm="24" :md="24" :lg="24" :xl="18">
        <Card shadow v-height="480">
          <template #title>
            <Avatar size="small" icon="ios-pulse" style="color: #1890ff; background: #e6f7ff" />
            <span class="ivu-pl-8">最近1小时访问情况</span>
          </template>
          <ChartLine ref="chartLine" :height="400" :options="lineOptions" />
        </Card>
      </Col>
      <Col :xs="24" :sm="24" :md="24" :lg="24" :xl="6">
        <Row :gutter="16">
          <Col class="ivu-mb" :xs="24" :sm="12" :md="24" :lg="12" :xl="24">
            <Card shadow v-height="232">
              <template #title>
                <Avatar size="small" icon="md-bulb" style="color: #ffa940; background: #fff7e6" />
                <span class="ivu-pl-8">在线人数</span>
              </template>
              <div class="ivu-text-center">
                <span class="ivu-flex-center"><NowTime onlyTime /></span>
                <CountUp :end="count" v-font="48" />
                <p>在线访客数</p>
                <p class="ivu-mt-8" v-font="12">
                  <Badge status="processing" />
                  <CountDown :target="limitTime" :format="formatNumber" @on-end="handleUpdateData" />
                  秒后更新
                </p>
              </div>
            </Card>
          </Col>
          <Col class="ivu-mb" :xs="24" :sm="12" :md="24" :lg="12" :xl="24">
            <Card shadow v-height="232">
              <template #title>
                <Avatar size="small" icon="ios-cafe" style="color: #eb2f96; background: #fff0f6" />
                <span class="ivu-pl-8">用户活跃度</span>
              </template>
              <div class="ivu-text-center">
                <Row align="middle" justify="center">
                  <Col span="14">
                    <i-circle :percent="percent1" stroke-color="rgb(47, 194, 91)" trail-color="rgb(246, 255, 237)">
                      <i-circle :percent="percent2" :size="95" stroke-color="rgb(24, 144, 255)" trail-color="rgb(230, 247, 255)">
                        <i-circle :percent="percent3" :size="70" stroke-color="rgb(240, 72, 100)" trail-color="rgb(255, 241, 240)"></i-circle>
                      </i-circle>
                    </i-circle>
                  </Col>
                  <Col span="10" class="ivu-text-left ivu-pl-4">
                    <Badge color="#2fc25b" :text="`活跃率：${percent1}%`" />
                    <Badge color="#1890ff" :text="`留存率：${percent2}%`" />
                    <Badge color="#f04864" :text="`跳出率：${percent3}%`" />
                  </Col>
                </Row>
              </div>
            </Card>
          </Col>
        </Row>
      </Col>
    </Row>
  </div>
</template>

<script>
  import { Circle, CountDown, CountUp, Trend } from 'view-ui-plus'
  import { debounce } from '@/utils/util'
  import { MonitorCard } from './component'
  import { ChartLine, NowTime } from '@/components'

  const randomNumber = (a, b) => Math.round(a * Math.random() + b)
  const fixedZero = value => {
    const date = new Date(value)
    const hour = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours()
    const minute = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes()
    return `${hour}:${minute}`
  }
  const handleGetData = () => {
    const result = []
    const y1Data = []
    const y2Data = []
    const time = new Date().getTime()
    for (let i = 0; i < 11; i++) {
      result.push(time - 1000 * 60 * 5 * i)
      y1Data.push(Math.ceil(Math.random() * 400) + 100)
      y2Data.push(Math.ceil(Math.random() * 90) + 10)
    }
    const xData = result.map(item => fixedZero(item)).reverse()
    return {
      xData,
      y1Data,
      y2Data
    }
  }
  export default {
    name: 'Monitor',
    components: {
      iCircle: Circle,
      ChartLine,
      CountDown,
      CountUp,
      MonitorCard,
      NowTime,
      Trend
    },
    data() {
      return {
        avatarList: [
          {
            src: 'https://dev-file.iviewui.com/BbnuuEiM0QXNPHVCvb3E2AFrawIjCkqW/avatar',
            tip: '史蒂夫·乔布斯'
          },
          {
            src: 'https://dev-file.iviewui.com/zhj85zgAfEjChCNIKT1LQENUIOyOYCaX/avatar',
            tip: '斯蒂夫·沃兹尼亚克'
          },
          {
            src: 'https://dev-file.iviewui.com/TkH54UozsINlex15TAMI00GElsfsKSiC/avatar',
            tip: '乔纳森·伊夫'
          },
          {
            src: 'https://dev-file.iviewui.com/xrzbBR99F6tYsDJPLNrvwhllowbuL7Gw/avatar',
            tip: '蒂姆·库克'
          },
          {
            src: 'https://dev-file.iviewui.com/bgrngoUb9A6UQ2kAwBFtnSNzhrh2qj1O/avatar',
            tip: '比尔·费尔南德斯'
          },
          {
            src: 'https://dev-file.iviewui.com/BbnuuEiM0QXNPHVCvb3E2AFrawIjCkqW/avatar',
            tip: '史蒂夫·乔布斯'
          },
          {
            src: 'https://dev-file.iviewui.com/zhj85zgAfEjChCNIKT1LQENUIOyOYCaX/avatar',
            tip: '斯蒂夫·沃兹尼亚克'
          },
          {
            src: 'https://dev-file.iviewui.com/TkH54UozsINlex15TAMI00GElsfsKSiC/avatar',
            tip: '乔纳森·伊夫'
          },
          {
            src: 'https://dev-file.iviewui.com/xrzbBR99F6tYsDJPLNrvwhllowbuL7Gw/avatar',
            tip: '蒂姆·库克'
          },
          {
            src: 'https://dev-file.iviewui.com/bgrngoUb9A6UQ2kAwBFtnSNzhrh2qj1O/avatar',
            tip: '比尔·费尔南德斯'
          }
        ],
        lineOptions: handleGetData(),
        count: randomNumber(900, 100),
        limitTime: new Date().getTime() + 5000,
        percent1: 55,
        percent2: 30,
        percent3: 15
      }
    },
    methods: {
      handleUpdateData() {
        this.count = randomNumber(900, 100)
        this.lineOptions = handleGetData()
        this.percent1 = randomNumber(100, 0)
        this.percent2 = randomNumber(100 - this.percent1, 0)
        this.percent3 = 100 - this.percent1 - this.percent2
        this.limitTime = new Date().getTime() + 5000
      },
      formatNumber(value) {
        return parseInt(value / 1000 + 1)
      },
      handleResize() {
        debounce(this.$refs.chartLine?.handleResize())
      }
    }
  }
</script>

<style lang="less" scoped></style>
