<template>
  <div class="extend-component">
    <div class="ivu-mt ivu-mb ivu-text-center">
      <SvgLogo />
      <h2>Welcome to {{ $config.titleSuffix }} App Components</h2>
    </div>
    <br />

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">VueXgplayer 视频播放 <Rate :modelValue="1" disabled allow-half /></h2>
      </template>
      <Row type="flex" justify="center" align="middle" class="ivu-text-left">
        <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
          <Space class="ivu-mb-8 ivu-text-left">
            <Button type="primary" ghost size="small" icon="ios-play" @click="play">播放</Button>
            <Button type="primary" ghost size="small" icon="md-pause" @click="pause">暂停</Button>
            <Button type="primary" ghost size="small" icon="md-refresh" @click="replay">重新播放</Button>
            <Button type="primary" ghost size="small" icon="md-repeat" @click="changeSrc">切换视频源</Button>
          </Space>
          <VueXgplayer :config="config" @player="handlePlayer" />
        </Col>
      </Row>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Tour 漫游式引导 <Rate :modelValue="3" disabled allow-half /></h2>
        <span>概述：用于分步引导用户了解产品功能的气泡组件。</span>
      </template>
      <Divider>基本使用</Divider>
      <Space :size="24">
        <Button type="dashed" @click="current1 = 0">开始引导</Button>
        <Button ref="uploadRef1">Upload</Button>
        <Button ref="saveRef1" type="primary">Save</Button>
        <Button ref="moreRef1">More</Button>
        <Tour v-model="current1" :steps="steps1" />
      </Space>
      <Divider>不带遮罩层</Divider>
      <Space :size="24">
        <Button type="dashed" @click="current2 = 0">开始引导</Button>
        <Button ref="uploadRef2">Upload</Button>
        <Button ref="saveRef2" type="primary">Save</Button>
        <Button ref="moreRef2">More</Button>
        <Tour v-model="current2" :steps="steps2" :mask="false" />
      </Space>
      <Divider>混合弹窗等多种形式</Divider>
      <Space :size="24">
        <Button type="dashed" @click="current3 = 0">开始引导</Button>
        <Button ref="uploadRef3">Upload</Button>
        <Button ref="saveRef3" type="primary">Save</Button>
        <Button ref="moreRef3">More</Button>
        <Tour v-model="current3" :steps="steps3">
          <template #text="{ step }">
            <div v-if="step.index === 0" class="ivu-mb">
              <img
                style="width: 100%; height: 184px; object-fit: cover"
                src="https://gw.alipayobjects.com/mdn/rms_08e378/afts/img/A*zx7LTI_ECSAAAAAAAAAAAABkARQnAQ"
              />
            </div>
            <div>{{ step.description }}</div>
          </template>
        </Tour>
      </Space>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Markdown编辑器 <Rate :modelValue="2" disabled allow-half /></h2>
      </template>
      <div style="text-align: initial">
        <Divider>ByteMdEditor内容编辑</Divider>
        <ByteMdEditor v-model="text" />
        <Divider>ByteMdViewer内容预览</Divider>
        <ByteMdViewer :modelValue="text" />
      </div>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">TinymceEditor富文本编辑器 <Rate :modelValue="2" disabled allow-half /></h2>
      </template>
      <TinymceEditor v-model="text" />
      <Divider>获取到的文本内容</Divider>
      <Input :modelValue="text" type="textarea" readonly show-word-limit :rows="5" />
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">DialogContainer 对话框容器 <Rate :modelValue="4" disabled allow-half /></h2>
        <span>概述：对话框容器，模态框和抽屉的二次封装，可根据需求快速切换。</span>
      </template>
      <Space :size="24">
        <RadioGroup v-model="DialogContainerType">
          <Radio border label="modal">模态框</Radio>
          <Radio border label="drawer">抽屉</Radio>
        </RadioGroup>
        <Button @click="formVisible = true">打开对话框</Button>
      </Space>
      <DialogContainer title="会议摘要" :type="DialogContainerType" :width="520" class="ivu-text-left" v-model="formVisible">
        <Form label-position="top">
          <FormItem prop="title" label="会议名称">
            <Input placeholder="会议的名称" />
          </FormItem>
          <FormItem prop="content" label="会议内容">
            <Input placeholder="会议的内容" />
          </FormItem>
          <FormItem prop="date" label="起止时间">
            <DatePicker style="width: 100%" type="datetimerange" placeholder="选择会议的起止时间" />
          </FormItem>
          <FormItem prop="room" label="会议地点">
            <Select placeholder="请选择会议室">
              <Option value="1">1号会议室</Option>
              <Option value="2">2号会议室</Option>
              <Option value="3">3号会议室</Option>
              <Option value="4">4号会议室</Option>
              <Option value="5">5号会议室</Option>
            </Select>
          </FormItem>
          <FormItem label="参会者（选填）" prop="users">
            <Select multiple placeholder="请选择参会人员">
              <Option value="逢跪跪">逢跪跪</Option>
              <Option value="中小鱼">中小鱼</Option>
              <Option value="唐不苦">唐不苦</Option>
            </Select>
          </FormItem>
          <FormItem label="备注（选填）" prop="remark">
            <Input :rows="3" type="textarea" placeholder="备注" show-word-limit :maxlength="200" />
          </FormItem>
        </Form>
      </DialogContainer>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Space 间距 <Rate :modelValue="5" disabled allow-half /></h2>
        <span>概述：基础组件，设置组件之间的间距。</span>
      </template>
      <Space direction="vertical" size="large" type="flex">
        <RadioGroup v-model="size">
          <Radio label="small">Small</Radio>
          <Radio label="middle">Middle</Radio>
          <Radio label="large">Large</Radio>
        </RadioGroup>
        <Space :size="size">
          <Button type="primary">Primary</Button>
          <Button>Default</Button>
          <Button type="dashed">Dashed</Button>
          <Button type="text">Text</Button>
        </Space>
        <Slider v-model="customSize" />
        <Space :size="customSize">
          <Button type="primary">Primary</Button>
          <Button>Default</Button>
          <Button type="dashed">Dashed</Button>
          <Button type="text">Text</Button>
        </Space>
      </Space>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Empty 空状态 <Rate :modelValue="4" disabled allow-half /></h2>
        <span>概述：当目前没有数据时，用于显式的用户提示。</span>
      </template>
      <Empty />
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Region 行政区域选择器 <Rate :modelValue="1" disabled allow-half /></h2>
        <span>概述：行政区域选择组件，自带国内数据（精确到街道，支持自定义数据），常用于地址选择等业务场景，支持 View UI 的 Form 验证。</span>
      </template>
      <Row type="flex" justify="center" align="middle" class="ivu-text-left">
        <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="8">
          <Divider>Region 行政区域选择器</Divider>
          <Region clearable />
          <Divider>City 城市选择器</Divider>
          <p class="ivu-mb">快速选择中国城市，常用于地图、旅游等基于位置的城市选择场景。</p>
          <City clearable />
        </Col>
      </Row>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">IconSelect 图标选择器 <Rate :modelValue="1" disabled allow-half /></h2>
        <span
          >概述：图标选择器组件，支持 iView 内置图标以及用户自定义的图标，支持搜索，支持 v-model 双向绑定数据，支持 View UI 的 Form
          验证。图标选择器组件还同时还支持 清空、禁用（也支持 Form 全部禁用）等常见表单类组件功能。</span
        >
      </template>
      <Row type="flex" justify="center" align="middle" class="ivu-text-left">
        <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="8">
          <IconSelect clearable />
        </Col>
      </Row>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">TabsNav 多页签 <Rate :modelValue="3" disabled allow-half /></h2>
      </template>

      <Space wrap :size="[16, 8]">
        <Button @click="$tabsNav.close()">关闭当前页</Button>
        <Button @click="$tabsNav.open('/user/profile')">打开个人中心</Button>
        <Button @click="$tabsNav.close('/user/profile')">关闭个人中心</Button>
        <Poptip confirm @on-ok="confirm">
          <template #title>
            <Form inline>
              <FormItem label="自定义名称">
                <Input v-model="newTab" placeholder="请输入新的Tab名称" />
              </FormItem>
            </Form>
          </template>
          <Button>修改当前Tab名称</Button>
        </Poptip>
        <Poptip confirm @on-ok="confirm2">
          <template #title>
            <div>
              <p>页面KEY是由页面的路由 <code>path</code> 决定的</p>
              <p>如果要修改某一个页面标题，该页面必须已经被打开在Tab栏</p>
              <p>后期可以考虑优化到编程式Tab栏，就可以没有这种限制</p>
              <Form inline>
                <FormItem label="页面KEY">
                  <Input v-model="tabKey" />
                </FormItem>
                <FormItem label="自定义名称">
                  <Input v-model="tabName" placeholder="请输入新的 Tab 名称" />
                </FormItem>
              </Form>
            </div>
          </template>
          <Button>修改某一个 Tab 名称</Button>
        </Poptip>
      </Space>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">VxeLoading 加载中 <Rate :modelValue="5" disabled allow-half /></h2>
      </template>
      <div v-height="100" style="position: relative">
        <vxe-loading />
      </div>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Ellipsis 文本自动省略号 <Rate :modelValue="5" disabled allow-half /></h2>
        <span>概述：文本过长自动处理省略号，支持按照文本长度、最大行数、限制高度三种方式截取。</span>
      </template>
      <div style="max-width: 600px" class="ivu-text-left ivu-margin-center">
        <Divider>按照字符数省略</Divider>
        <Ellipsis :text="text" :length="100" />

        <Divider>显示 Tooltip</Divider>
        <Ellipsis :text="text" :length="100" tooltip />

        <Divider>按照高度省略</Divider>
        <Ellipsis :text="text" :height="100" />

        <Divider>按照行数省略</Divider>
        <Ellipsis :text="text" :lines="2" />
      </div>
    </Card>
  </div>
</template>

<script>
  import { Rate, Slider } from 'view-ui-plus'
  import { City, Tour, Region, Ellipsis, SvgLogo, IconSelect, VxeLoading, VueXgplayer, ByteMdEditor, ByteMdViewer, TinymceEditor } from '@/components'

  export default {
    name: 'Components',
    components: {
      City,
      Tour,
      Rate,
      Region,
      Slider,
      SvgLogo,
      Ellipsis,
      IconSelect,
      VxeLoading,
      VueXgplayer,
      ByteMdEditor,
      ByteMdViewer,
      TinymceEditor
    },
    data() {
      return {
        text:
          '时维九月，序属三秋。潦水尽而寒潭清，烟光凝而暮山紫。俨骖騑于上路，访风景于崇阿；临帝子之长洲，得天人之旧馆。层峦耸翠，上出重霄；飞阁流丹，下临无地。鹤汀凫渚，穷岛屿之萦回；桂殿兰宫，即冈峦之体势。披绣闼，俯雕甍，山原旷其盈视，川泽纡其骇瞩。闾阎扑地，钟鸣鼎食之家；舸舰弥津，青雀黄龙之舳。云销雨霁，彩彻区明。落霞与孤鹜齐飞，秋水共长天一色。渔舟唱晚，响穷彭蠡之滨；雁阵惊寒，声断衡阳之浦。',
        newTab: '',
        tabKey: '/dashboard/workplace',
        tabName: '',
        size: 'small',
        customSize: 8,
        formVisible: false,
        DialogContainerType: 'modal',
        current1: null,
        current2: null,
        current3: null,
        steps1: [
          {
            target: () => this.$refs.uploadRef1?.$el,
            title: '如何进行文件上传',
            description: '点击这个按钮在弹出框中选择想要上传的文件即可.'
          },
          {
            target: () => this.$refs.saveRef1?.$el,
            title: '如何提交数据',
            description: '数据录入完成后点击这个按钮即可提交数据到后台.'
          },
          {
            target: () => this.$refs.moreRef1?.$el,
            title: '如何进行更多的操作',
            description: '鼠标移入到此按钮上即可展示出更多的操作功能.'
          }
        ],
        steps2: [
          {
            target: () => this.$refs.uploadRef2?.$el,
            title: '如何进行文件上传',
            description: '点击这个按钮在弹出框中选择想要上传的文件即可.',
            popoverProps: {
              placement: 'top-start'
            }
          },
          {
            target: () => this.$refs.saveRef2?.$el,
            title: '如何提交数据',
            description: '数据录入完成后点击这个按钮即可提交数据到后台.',
            popoverProps: {
              placement: 'bottom'
            }
          },
          {
            target: () => this.$refs.moreRef2?.$el,
            title: '如何进行更多的操作',
            description: '鼠标移入到此按钮上即可展示出更多的操作功能.',
            popoverProps: {
              placement: 'top-end'
            }
          }
        ],
        steps3: [
          {
            title: '欢迎使用 iView Admin Pro 系统',
            description: '下面将为您介绍一些常用功能的操作说明, 如果之前已经为您介绍过, 您可以直接点击跳过结束指引.'
          },
          {
            target: () => this.$refs.uploadRef3?.$el,
            title: '如何进行文件上传',
            description: '点击这个按钮在弹出框中选择想要上传的文件即可.'
          },
          {
            target: () => this.$refs.saveRef3?.$el,
            title: '如何提交数据',
            description: '数据录入完成后点击这个按钮即可提交数据到后台.',
            mask: false
          },
          {
            target: () => this.$refs.moreRef3?.$el,
            title: '如何进行更多的操作',
            description: '鼠标移入到此按钮上即可展示出更多的操作功能.'
          }
        ],
        config: {
          id: 'antd',
          loop: true,
          url: 'https://cdn.cnbj1.fds.api.mi-img.com/aife/xiaoai-open-platform/xiaoai6/evolution/h5-evolution-video.mp4'
        },
        player: null,
        ready: false
      }
    },
    methods: {
      confirm() {
        if (!this.newTab) {
          this.$Message.warning('请输入新的 Tab 名称')
        }
        const { path } = this.$route
        this.$tabsNav.rename(path, this.newTab)
      },
      confirm2() {
        this.$tabsNav.rename(this.tabKey, this.tabName)
      },
      handlePlayer(player) {
        this.player = player
        this.player.on('play', () => {
          this.ready = true
        })
      },
      play() {
        this.player.paused && this.player.play()
      },
      pause() {
        this.player.paused || this.player.pause()
      },
      replay() {
        this.player.replay()
      },
      changeSrc() {
        const srcArray = [
          this.config.url,
          'https://cdn.cnbj1.fds.api.mi-img.com/product-images/xiaomibuds4prova5b03//modal.mp4',
          'https://cdn.cnbj1.fds.api.mi-img.com/product-images/xiaomibuds4teqz85/video_buds_4.mp4'
        ].filter(item => item !== this.player.src)
        this.player.src = srcArray[Math.floor(Math.random() * srcArray.length)]
        this.player.paused && this.player.play()
      }
    }
  }
</script>

<style lang="less">
  .extend-component {
    .ivu-mb.ivu-text-center {
      svg {
        width: 48px;
        height: 48px;
      }

      h2 {
        text-shadow: 0 6px 2px rgba(0, 0, 0, 0.3);
      }
    }

    .ivu-card {
      margin-bottom: 16px;
      text-align: center;

      &-head-title {
        display: block;

        & > span {
          color: #515a6e;
          font-size: 14px;
        }
      }
    }
  }
</style>
