<template>
  <div>
    <Card class="ivu-mb" shadow>
      <div class="workplace-header">
        <div class="workplace-header-left">
          <RouterLink to="/user/profile">
            <Avatar :src="userInfo.avatar" :size="72" />
          </RouterLink>
          <div class="header-content">
            <div class="header-content-title">
              {{ timeFix }}，{{ userInfo.name }}<span class="welcome-text">，{{ welcome }}</span>
            </div>
            <div class="header-content-desc">前端工程师 | 中台-数据平台团队-前端创新团队</div>
          </div>
        </div>
        <Row class="workplace-header-right">
          <Col span="8">
            <div class="right-item-title">
              <Avatar size="small" icon="md-apps" :style="{ color: `#40A9FF`, backgroundColor: `#E6F7FF` }" />
              <span>项目数</span>
            </div>
            <div class="right-item-count">56</div>
          </Col>
          <Col span="8">
            <div class="right-item-title">
              <Avatar size="small" icon="md-trending-up" :style="{ color: `#FFA940`, backgroundColor: `#FFF7E6` }" />
              <span>团队排名</span>
            </div>
            <div class="right-item-count">8/24</div>
          </Col>
          <Col span="8">
            <div class="right-item-title">
              <Avatar size="small" icon="logo-yen" :style="{ color: `#73D13D`, backgroundColor: `#F6FFED` }" />
              <span>总收入</span>
            </div>
            <div class="right-item-count">3,256</div>
          </Col>
        </Row>
      </div>
    </Card>
    <div>
      <Row :gutter="16">
        <Col :xl="16" :lg="24" :md="24" :sm="24" :xs="24">
          <Card shadow :padding="0" class="ivu-workplace-project">
            <template #title>
              <Avatar size="small" icon="md-apps" style="color: #1890ff; background: #e6f7ff" />
              <span class="ivu-pl-8">我的项目</span>
            </template>
            <template #extra>
              <RouterLink to="/">全部项目</RouterLink>
            </template>
            <Spin fix v-if="loading" />
            <Grid :hover="true" :col="isMobile ? 1 : 3" :class="isMobile && 'mobile'">
              <GridItem v-for="(item, index) in projects" :key="index">
                <div class="ivu-card-meta-detail">
                  <div class="card-title">
                    <Avatar size="small" :src="item.cover" />
                    <a>{{ item.title }}</a>
                  </div>
                  <div class="card-description">
                    {{ item.description }}
                  </div>
                </div>
                <div class="project-item">
                  <a href="#">科学搬砖组</a>
                  <span class="datetime">{{ $Date(item.updatedAt).fromNow() }}</span>
                </div>
              </GridItem>
            </Grid>
          </Card>

          <Card shadow class="ivu-workplace-dynamic">
            <template #title>
              <Avatar size="small" icon="md-bulb" style="color: rgb(114, 46, 209); background: rgb(249, 240, 255)" />
              <span class="ivu-pl-8">动态</span>
            </template>
            <List size="large">
              <ListItem :key="index" v-for="(item, index) in activities">
                <ListItemMeta :avatar="item.user.avatar" :description="item.time">
                  <template #title>
                    <span>{{ item.user.nickname }}</span
                    >&nbsp; 在&nbsp;<a href="#">{{ item.project.name }}</a
                    >&nbsp; <span>{{ item.project.action }}</span
                    >&nbsp;
                    <a href="#">{{ item.project.event }}</a>
                  </template>
                </ListItemMeta>
              </ListItem>
            </List>
          </Card>
        </Col>
        <Col :xl="8" :lg="24" :md="24" :sm="24" :xs="24">
          <Card shadow class="ivu-workplace-shortcut">
            <template #title>
              <Avatar size="small" icon="md-paper-plane" style="color: rgb(255, 77, 79); background: rgb(255, 241, 240)" />
              <span class="ivu-pl-8">快捷导航</span>
            </template>
            <template #extra>
              <Tooltip transfer placement="top">
                <template #content>配置</template>
                <Icon type="md-settings" class="ivu-link" @click="onOpenModal" />
              </Tooltip>
            </template>
            <Grid class="ivu-text-center" :col="4" :border="false" padding="18px">
              <GridItem v-for="item in shortcuts" :key="item.name">
                <a :href="item.path" class="ivu-link ivu-link-color">
                  <XIcon :type="item.icon" size="24" :style="{ color: item.color }" />
                  <p class="ivu-pt-8">{{ item.name }}</p>
                </a>
              </GridItem>
            </Grid>
          </Card>
          <Modal v-model="showModal" title="添加快捷导航">
            <Form label-position="top" ref="formValidate" :model="formValidate" :rules="ruleValidate">
              <FormItem prop="name" label="名称">
                <Input v-model="formValidate.name" placeholder="名称" />
              </FormItem>
              <FormItem prop="path" label="链接">
                <Input v-model="formValidate.path" placeholder="链接" />
              </FormItem>
            </Form>
            <template #footer>
              <Button type="text" @click="cancel">取消</Button>
              <Button type="primary" @click="ok">确定</Button>
            </template>
          </Modal>
          <Card shadow :padding="1" class="ivu-workplace-todo">
            <template #title>
              <Avatar size="small" icon="md-checkbox-outline" style="color: rgb(255, 169, 64); background: rgb(255, 247, 230)" />
              <span class="ivu-pl-8">待办事项</span>
            </template>
            <Table :columns="tableColumns" :data="tableData" :draggable="true">
              <template #task="{ row, index }">
                <Tooltip :content="row.task" :disabled="!showTooltip" transfer placement="top" :max-width="300" class="ivu-table-cell-tooltip">
                  <span :ref="`content${index}`" @mouseenter="handleTooltipIn(index)" class="ivu-table-cell-tooltip-content">{{ row.task }}</span>
                </Tooltip>
              </template>
            </Table>
          </Card>

          <Card shadow class="ivu-workplace-members">
            <template #title>
              <Avatar size="small" icon="ios-people" style="color: rgb(82, 196, 26); background: rgb(246, 255, 237)" />
              <span class="ivu-pl-8">小组成员</span>
            </template>
            <Row v-for="item in teamNumbers" :key="item.name">
              <Col :span="5" class="ivu-text-center">
                <Avatar :src="item.avatar" />
              </Col>
              <Col :span="15">
                <div>
                  <strong>{{ item.name }}</strong>
                </div>
                <div>
                  <span style="font-size: 12px; color: rgb(128, 134, 149)">{{ item.job }}</span>
                </div>
              </Col>
              <Col :span="4">
                <Tag :color="item.status === `在线` ? `green` : `red`">{{ item.status }}</Tag>
              </Col>
            </Row>
          </Card>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import { Grid, GridItem } from 'view-ui-plus'
  import { timeFix } from '@/utils/util'
  import { mixinDevice } from '@/utils/mixin'
  import { getRoleList, getServiceList } from '@/api/manage'

  export default {
    name: 'Workplace',
    components: {
      Grid,
      GridItem
    },
    mixins: [mixinDevice],
    data() {
      return {
        timeFix: timeFix(),
        user: {},

        projects: [],
        loading: true,
        activities: [],

        // 快捷操作
        shortcuts1: [
          {
            icon: 'i-icon-demo dashboard',
            name: '主控台',
            path: '',
            color: 'rgb(82, 196, 26)'
          },
          {
            icon: 'i-icon-demo form',
            name: '表单',
            path: '',
            color: 'rgb(24, 144, 255)'
          },
          {
            icon: 'i-icon-demo list',
            name: '列表',
            path: '',
            color: 'rgb(250, 173, 20)'
          },
          {
            icon: 'i-icon-demo search',
            name: '搜索',
            path: '',
            color: 'rgb(114, 46, 209)'
          },
          {
            icon: 'i-icon-demo setting',
            name: '设置',
            path: '',
            color: 'rgb(19, 194, 194)'
          },
          {
            icon: 'i-icon-demo tool',
            name: '工具',
            path: '',
            color: 'rgb(235, 47, 150)'
          }
        ],
        shortcuts: [
          {
            icon: 'md-people',
            name: '用户',
            path: '',
            color: 'rgb(105, 192, 255)'
          },
          {
            icon: 'md-podium',
            name: '分析',
            path: '',
            color: 'rgb(149, 222, 100)'
          },
          {
            icon: 'md-cart',
            name: '商品',
            path: '',
            color: 'rgb(255, 156, 110)'
          },
          {
            icon: 'md-clipboard',
            name: '订单',
            path: '',
            color: 'rgb(179, 127, 235)'
          },
          {
            icon: 'md-card',
            name: '票据',
            path: '',
            color: 'rgb(255, 214, 102)'
          },
          {
            icon: 'md-mail',
            name: '消息',
            path: '',
            color: 'rgb(92, 219, 211)'
          },
          {
            icon: 'md-pricetags',
            name: '标签',
            path: '',
            color: 'rgb(255, 133, 192)'
          },
          {
            icon: 'md-switch',
            name: '配置',
            path: '',
            color: 'rgb(255, 192, 105)'
          }
        ],
        showModal: false,
        formValidate: {
          name: '',
          path: ''
        },
        ruleValidate: {
          name: [{ required: true, message: '名称不能为空', trigger: 'blur' }],
          path: [{ required: true, message: '链接不能为空', trigger: 'blur' }]
        },

        tableColumns: [
          {
            type: 'selection',
            width: 52,
            align: 'center'
          },
          {
            title: '任务',
            key: 'task',
            slot: 'task'
          },
          {
            title: '协作者',
            width: 100,
            key: 'together'
          }
        ],
        tableData: [
          {
            id: 1,
            task: 'Card 支持点击，可以配置 to 等属性',
            together: 'New York'
          },
          {
            id: 2,
            task: 'Tabs 新增属性，高度可以自适应其它高度',
            together: 'New York'
          },
          {
            id: 3,
            task: 'AvatarList 支持配置 extra，不一定给全量数据',
            together: 'New York'
          },
          {
            id: 4,
            task: 'Drawer 新增可拖拽调整宽度的属性',
            together: 'New York'
          }
        ],
        showTooltip: false,

        teamNumbers: [
          {
            avatar: 'https://dev-file.iviewui.com/userinfoYLhfo1S945BOLuFT96NRStYeYDFRviF5/avatar',
            name: '中小鱼',
            job: 'Designer',
            status: '在线'
          },
          {
            avatar: 'https://dev-file.iviewui.com/userinfoxlXwHVwZkCQtl1Zyd1wrvF78b1rZkhfK/avatar',
            name: 'Echo',
            job: 'Developer',
            status: '在线'
          },
          {
            avatar: 'https://dev-file.iviewui.com/userinfosvaY5tb7yfnSFTTimcjy3vuSG6RC28v2/avatar',
            name: '唐不苦',
            job: 'Product Manager',
            status: '离线'
          },
          {
            avatar: 'https://dev-file.iviewui.com/userinforaP8NeQgYpmKgkpWlqZP7rfewbHiIzJY/avatar',
            name: '甜筒',
            job: 'Manager',
            status: '在线'
          },
          {
            avatar: 'https://dev-file.iviewui.com/userinfohRf8MIX3RrQHbT9k2aX05v1pTwl0owTh/avatar',
            name: '蓝莓酱',
            job: 'Manager',
            status: '离线'
          }
        ]
      }
    },
    computed: {
      ...mapState({
        welcome: state => state.user.welcome,
        userInfo: state => state.user.userInfo
      })
    },
    created() {
      this.user = this.userInfo

      getRoleList().then(res => {
        // console.log('workplace -> call getRoleList()', res)
      })

      getServiceList().then(res => {
        // console.log('workplace -> call getServiceList()', res)
      })
    },
    mounted() {
      this.getProjects()
      this.getActivity()
    },
    methods: {
      onOpenModal() {
        this.cancel()
        this.$refs.formValidate.resetFields()
      },
      ok() {
        this.$refs.formValidate.validate(valid => {
          if (!valid) {
            return false
          }
        })
      },
      cancel() {
        this.showModal = !this.showModal
      },
      handleTooltipIn(index) {
        const content = this.$refs[`content${index}`]
        let range = document.createRange()
        range.setStart(content, 0)
        range.setEnd(content, content.childNodes.length)
        const rangeWidth = range.getBoundingClientRect().width
        this.showTooltip = rangeWidth > content.offsetWidth
        range = null
      },
      getProjects() {
        this.$axios.get('/list/search/projects').then(res => {
          this.projects = res.data
          this.loading = false
        })
      },
      getActivity() {
        this.$axios.get('/workplace/activity').then(res => {
          this.activities = res
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .workplace-header {
    display: flex;
    align-items: center;

    &-left {
      flex: 1;
      display: flex;
      align-items: center;

      & > span {
        flex-shrink: 0;
      }

      .header-content {
        padding-left: 24px;

        .header-content-title {
          font-size: 20px;
          font-weight: 500;
          margin-bottom: 8px;
        }

        .header-content-desc {
          color: rgba(0, 0, 0, 0.45);
        }
      }
    }

    &-right {
      width: 375px;
      flex-shrink: 0;
      white-space: nowrap;
      text-align: right;

      .right-item-title {
        display: flex;
        align-items: center;
        justify-content: flex-end;

        span + span {
          color: rgba(0, 0, 0, 0.45);
          margin-left: 4px;
        }
      }

      .right-item-count {
        font-size: 24px;
        color: rgba(0, 0, 0, 0.85);
      }
    }
  }

  .ivu-workplace-project {
    position: relative;
    margin-bottom: 16px;

    :deep(.ivu-card-head) {
      border: 0;
    }

    :deep(.ivu-card-extra) {
      top: 16px;
    }

    .mobile {
      :deep(.ivu-GridItem) {
        box-shadow: 0 0 0 0 #e8eaec, 0 0 0 0 #e8eaec, 0 0 0 0 #e8eaec, 0 0 0 0 #e8eaec inset, 0 1px 0 0 #e8eaec inset;
      }
    }

    .ivu-card-meta-detail {
      overflow: hidden;

      .card-title {
        margin-bottom: 8px;
        font-size: 0;

        a {
          color: rgba(0, 0, 0, 0.85);
          margin-left: 12px;
          line-height: 24px;
          height: 24px;
          display: inline-block;
          vertical-align: top;
          font-size: 14px;

          &:hover {
            color: #2d8cf0;
          }
        }
      }

      .card-description {
        color: rgba(0, 0, 0, 0.45);
        height: 44px;
        line-height: 22px;
        overflow: hidden;
      }
    }

    .project-item {
      display: flex;
      margin-top: 8px;
      overflow: hidden;
      font-size: 12px;
      height: 20px;
      line-height: 20px;

      a {
        color: rgba(0, 0, 0, 0.45);
        display: inline-block;
        flex: 1 1 0;

        &:hover {
          color: #2d8cf0;
        }
      }

      .datetime {
        color: rgba(0, 0, 0, 0.25);
        flex: 0 0 auto;
        float: right;
      }
    }

    .ant-card-meta-description {
      color: rgba(0, 0, 0, 0.45);
      height: 44px;
      line-height: 22px;
      overflow: hidden;
    }
  }

  .ivu-workplace-dynamic {
    margin-bottom: 16px;

    :deep(.ivu-list-item-meta-avatar) {
      .ivu-avatar {
        width: 40px;
        height: 40px;
      }
    }

    :deep(.ivu-list-item-meta-title > a) {
      color: #2d8cf0;
    }
  }

  .ivu-workplace-shortcut {
    margin-bottom: 16px;

    :deep(.ivu-card-extra) {
      top: 16px;
    }

    :deep(.ivu-card-body) {
      .ivu-GridItem-main {
        & > a:hover {
          color: #2d8cf0;
        }
      }

      .ivu-divider {
        margin: 8px 0;
      }
    }
  }

  .ivu-workplace-todo {
    margin-bottom: 16px;

    :deep(.ivu-table) {
      &:before {
        height: 0;
      }

      &:before,
      th {
        background: #fff;
      }

      & tr:last-child td {
        border: 0;
      }

      .ivu-table-cell {
        padding: 0 8px;
      }
    }
  }

  .ivu-workplace-members {
    margin-bottom: 16px;

    :deep(.ivu-card-body) {
      .ivu-row {
        align-items: center;
        margin-bottom: 16px;

        &:last-child {
          margin-bottom: 0;
        }

        & > div:first-child {
          display: flex;
          justify-content: center;

          & > span {
            display: flex;
          }
        }
      }
    }
  }

  .mobile {
    .workplace-header {
      display: block;

      .welcome-text {
        display: none;
      }

      &-right {
        width: auto;
        text-align: center;
        margin-top: 16px;

        .right-item-title {
          justify-content: center;
        }
      }
    }

    .more-info {
      margin: 16px 0;
    }

    .headerContent .title .welcome-text {
      display: none;
    }
  }
</style>
