<template>
  <Card :title="title" shadow :padding="12">
    <div class="ivu-chart-title">
      <slot name="total"></slot>
    </div>
    <template #extra>
      <slot name="extra"></slot>
    </template>
    <slot></slot>
    <Divider class="ivu-mb-8 ivu-mt-8" />
    <slot name="footer"></slot>
  </Card>
</template>

<script>
  import { Card, Divider } from 'view-ui-plus'

  export default {
    name: 'ChartCard',
    components: { Card, Divider },
    props: {
      title: {
        type: String,
        default: ''
      }
    }
  }
</script>
<style lang="less">
  .ivu-chart-title {
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    white-space: nowrap;
    color: #000;
    margin-bottom: 0;
    font-size: 30px;
  }
</style>
