<template>
  <div class="visit-ranking" v-height="height">
    <ul>
      <li v-for="(item, index) in list" :key="index">
        <Row type="flex" align="middle" justify="center">
          <Col span="14">
            <Avatar v-color="index < 3 ? '#1890ff' : '#8c8c8c'" v-bg-color="index < 3 ? '#e6f7ff' : '#f5f5f5'" class="ivu-mr-8" size="small">
              {{ index + 1 }}
            </Avatar>
            <span>{{ item.name }}</span>
          </Col>
          <Col span="10" class="ivu-text-right">{{ item.total }}</Col>
        </Row>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'RankList',
    props: {
      height: {
        type: [Number, String],
        default: 'auto'
      },
      list: {
        type: Array,
        default: () => []
      }
    }
  }
</script>

<style lang="less">
  .visit-ranking {
    ul li {
      list-style-type: none;
      margin-top: 14px;
    }
  }
</style>
